#ember-body-content {
	&.ember-application {
		@import "../../base/main";
		.approvals-container {
			.approval-tabs-view {
				background-color: $white;
				padding-left: 46px;
				font-weight: $text-weight-semi-bold;
				.tabs__item .tabs__item__nav  {
					padding: 0;
					margin-right: 32px;
					.has-content-count::after {
						background-color: $white;
						border: 1px solid $border-light-color;
						padding: 2px 8px;
						box-shadow: 0 1px 6px $box-shadow-color-3;
					}
				}
			}
			#pending_approvals, #historical_approvals {
				padding: 0;
			}
			//approval lists
			.approval-list-wrapper {
				.approval-tab-header {
					display: flex;
					justify-content: space-between;
					height: 56px;
					background-color: $white;
					position: relative;
					box-shadow: 0px 4px 10px $box-shadow-color-3;
					.table-sort-dropdown-title {
						padding: 20px 0 0 54px;
						width: 270px;
						svg  {
							position: absolute;
							width: 14px;
							height: 14px;
							margin: 3px 0 0 5px;
						}
						span {
							padding-right: 3px;
						}
						.order-by-value {
							color: $app-icon-color;
							font-weight: $text-weight-semi-bold;
						}
					}
					.approval-sort-default-dropdown {
						cursor: pointer;
						float: left;
						.ember-basic-dropdown-content {
							min-width: 200px;
							margin-top: 6px;
							margin-left: 86px;
							.active {
								color: $linkColor;
								font-weight: $text-weight-medium;
								outline: none;
							}
							.break-section {
								margin: 6px 0;
							}
						}
					}
					.ember-pagination {
						height: auto;
						padding-right: 40px;
						display: flex;
						justify-content: flex-end;
					}
				}
				.agent-approval-list {
					background-color: $white;
					padding: 16px 36px;
					min-height: calc(100vh - 160px);
					//card view
					.approval-card-wrapper {
						margin-bottom: 8px;
						border: 1px solid $background-color;
						border-radius: 6px;
						box-shadow: 0px 4px 10px $box-shadow-color-3;
						cursor: pointer;
						.my-card {
							display: flex;
							padding: 16px 24px 0 16px;
							.card-left-section{
								flex: 55%;
								.heading {
									font-size: $title-small;
									font-weight: $text-weight-medium;
									line-height:  16px;
									border-right: 1px solid $background-color;

									.card-title {
										color: $linkColor;
									}
									.card-id {
										color: $app-icon-color;
									}
								}
								.section {
									line-height: 27px;
									font-size: $text-normal;
									border-right: 1px solid $background-color;
									margin-bottom: 7px;

									.middle-line {
										padding: 0 16px;
										color: $app-border-primary;
									}
									.right-arrow-section {
										float: left;
										padding-top: 3px;
										svg {
											width: 14px;
											height: 14px;
										}
									}
									.request-sent-details {
										color: $inputHoverBorder;
										font-weight: $text-weight-normal;
										padding-left: 7px;
										display: inline-block;
										width: 300px;
									}
									.view-message {
										color: $linkColor;
										font-weight: $text-weight-medium;
										cursor: pointer;
									}
								}
								.remarks-message {
									margin: 0 0 15px 21px;
									padding: 11px;
									background-color: $background-color-2;
									display: inline-block;
									border-radius: 0 8px 8px 8px;
									color: $inputHoverBorder;
								}
								.remove-border {
									border-right: none;
								}
								
							}
							.card-right-section {
								flex: 45%;
								.status-priority {
									float: left;
									padding: 0 16px;
									color: $secondary-text-color-2;
								}
								.status-priority-value {
									font-weight: $text-weight-medium;
									color: $app-icon-color;
									float: left;
									.priority-box {
										float: left;
										margin-top: 4px;
										margin-right: 6px;
									}
									@include priority-colors('.priority-color-1', '.priority-color-2', '.priority-color-3', '.priority-color-4');
									.priority-value {
										display: flex;
									}
								}
								.approve-reject-button {
									float: right;
									position: relative;
									.remarks-section {
										position: absolute;
										right: 0;
										top: 42px;
										width: 281px;
										padding: 16px;
										background: $white;
										box-shadow: 0px 2px 16px $dropdown-shadow-color;
										z-index: 99999;
										border-radius: 2px;
										.required-star {
											color: $app-red;
										}
										#public-approval-remark {
											width: 100%;
											height: 64px;
											margin-bottom: 9px;
										}
										label {
											padding-bottom: 8px;
											font-weight: $text-weight-semi-bold;
										}
										.save-cancel-section {
											display: flex;
											justify-content: flex-end;
											.save {
												background: $btnPrimaryBackground;
												color: $white;
												margin-left: 8px;
												font-weight: $text-weight-semi-bold;
												font-size: $title-small;
												border: 1px solid $primary-text-color-1;
											}
											.disable {
												pointer-events: none;
												border: 1px solid $btnDisabledColor;
												background: $disabled-text-color;
											}
											.cancel {
												background-color: $background-color-2;
												font-weight: $text-weight-semi-bold;
												font-size: $title-small;
												color: $primary-text-color-1;
											}
										}
										
									}

									button {
										min-width: 103px;
										height: 32px;
										border-radius: $radius;
										background-color: $white;
										border: 1px solid $app-border-primary;
										svg {
											width: 14px;
											height: 14px;
										}
										.approve-reject-icon {
											float: left;
											padding-left: 6px;
											margin-top: 2px;
										}
										.approve-reject-text {
											padding: 0 6px 0 2px;
											font-weight: $text-weight-semi-bold;
											font-size: $title-small;
										}
									}
									.reject-button {
										width: 88px;
										margin-left: 6px;
										color: $primary-text-color-1;
										&:hover {
											background-color: $border-light-color;
										}
									}
									.approve-button{
										color: $primary-text-color-1;
										&:hover {
											background-color: $border-light-color;
										}
									}
									.active {
										background-color: $border-light-color;
									}
								}
								
							}
							.approve {
								fill: $app-success-state;
							}
							.reject {
								fill: $app-error-state;
							}
						}

						.approve-reject-button{
							visibility: hidden;
						}
						.showHoverCard, &:hover .approve-reject-button{
							visibility: visible;
						}

						.card-detail {
							background-color: $background-color-2;
							padding: 24px 24px 15px;
							b {
								font-weight: $text-weight-semi-bold;
							}
						}
						
					}
					.no-approval {
						text-align: center;
						margin-top: 64px;
						.status {
							line-height: 20px;
							color: $app-form-label-color;
							margin-top: 25px;
						}
					}
				}
			}
			.shimmer-loader {
				background-color: $app-white;
			}
			
		}

		.approvals-menu-list-container {
			cursor: pointer;
			.menu-trigger-text{
				padding-left: 4px;
			}
			.approvals-item-all {
				margin-bottom: 17px;
			}
			.approvals-item-ticket {
				border-top: 1px solid $background-color;
			}
			.menuitem:nth-child(n+1) {
				font-weight: $text-weight-medium;
			}

		}
	}
}